// src/lib/firebase.js
import { initializeApp, getApps, getApp } from 'firebase/app'
import { getFirestore, serverTimestamp as ts } from 'firebase/firestore'
import {
  getAuth,
  setPersistence,
  browserLocalPersistence,
  inMemoryPersistence,
  signInAnonymously,
  onAuthStateChanged,
} from 'firebase/auth'

// ==========================================================
// Firebase è¨­å®š
// ==========================================================
const firebaseConfig = {
  apiKey:      import.meta.env.VITE_FB_API_KEY,
  authDomain:  import.meta.env.VITE_FB_AUTH_DOMAIN,
  projectId:   import.meta.env.VITE_FB_PROJECT_ID,
  appId:       import.meta.env.VITE_FB_APP_ID,
}

// æ¬ è½ãƒã‚§ãƒƒã‚¯
const missing = Object.entries(firebaseConfig)
  .filter(([, v]) => !v)
  .map(([k]) => k)

if (missing.length) {
  const msg = `[firebaseConfig] missing keys: ${missing.join(', ')}`
  if (import.meta.env.DEV) {
    throw new Error(msg) // é–‹ç™ºæ™‚ã¯å¼·åˆ¶ã‚¨ãƒ©ãƒ¼
  } else {
    console.error(msg)   // æœ¬ç•ªã¯ç”»é¢ã‚’æ­¢ã‚ãªã„
  }
}

// ==========================================================
// Firebase åˆæœŸåŒ–ï¼ˆHMR / å¤šé‡åˆæœŸåŒ–å¯¾ç­–ï¼‰
// ==========================================================
const app = getApps().length ? getApp() : initializeApp(firebaseConfig)
export const db = getFirestore(app)
export const auth = getAuth(app)
export { ts }

// ==========================================================
// åŒ¿åãƒ­ã‚°ã‚¤ãƒ³ã‚’å®‰å…¨ã«ç¢ºç«‹
// ==========================================================
async function waitForAuthReady(timeoutMs = 1500) {
  if (auth.currentUser !== null) return auth.currentUser
  await new Promise((resolve) => {
    const off = onAuthStateChanged(auth, () => { off(); resolve() })
    setTimeout(() => {
      try { off() } catch {}
      resolve()
    }, timeoutMs)
  })
  return auth.currentUser
}

export async function ensureAnonLogin() {
  // æ°¸ç¶šåŒ–ï¼ˆlocalStorageå„ªå…ˆ â†’ ãƒ€ãƒ¡ãªã‚‰ inMemoryï¼‰
  try {
    await setPersistence(auth, browserLocalPersistence)
  } catch (e) {
    console.warn('[auth] localPersistence NG â†’ inMemory', e?.code || e)
    try {
      await setPersistence(auth, inMemoryPersistence)
    } catch (e2) {
      console.error('[auth] setPersistence failed', e2?.code || e2)
    }
  }

  const ready = await waitForAuthReady()
  if (ready) return ready

  // ã‚ªãƒ•ãƒ©ã‚¤ãƒ³ãªã‚‰ã‚µã‚¤ãƒ³ã‚¤ãƒ³ã‚’è«¦ã‚ã‚‹
  if (typeof navigator !== 'undefined' && navigator.onLine === false) {
    console.warn('[auth] offline, skip signInAnonymously')
    return null
  }

  try {
    const cred = await signInAnonymously(auth)
    return cred.user
  } catch (e) {
    if (e?.code === 'auth/network-request-failed') {
      console.warn('[auth] network-request-failed, continue without auth')
      return null
    }
    console.error('[auth] anonymous sign-in failed', e?.code, e?.message || e)
    return null
  }
}
